@import "./operators-num-section/operators-num-section.css";

*,
body {
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  color: inherit;
  font-family: Arial, Helvetica, sans-serif;
  transition: all 0.5s;
}

body {
  height: 100vh;
  width: 100%;
  display: grid;
  align-content: space-around;
  justify-content: space-around;
}

.calculator-app {
  height: 80vh;
  width: 25vw;
  display: grid;
  grid-template-columns: 0.99fr;
  grid-template-rows: 20vh 0.99fr;
  align-content: space-around;
  justify-content: space-around;
  background-color: black;
  border-radius: 15px;
}

.result-section {
  display: grid;
  align-content: flex-end;
  justify-content: flex-end;
  border-bottom: 2px solid rgba(255, 255, 255, 0.5);
}

.result-section > h1 {
  color: white;
  font-weight: bolder;
  font-size: 3rem;
  letter-spacing: 2px;
  padding-right: 10px;
}

@media (min-width: 0px) and (max-width: 375px) {
  .calculator-app {
    height: 95vh;
    width: 90vw;
  }
}

@media (min-width: 376px) and (max-width: 425px) {
  .calculator-app {
    height: 95vh;
    width: 70vw;
  }
}

@media (min-width: 426px) and (max-width: 1040px) {
  .calculator-app {
    height: 95vh;
    width: 41vw;
  }
}
